import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore }from './../../state'

let auth = function(token){
  axios.request({
    url: endpointURL + '/authtoken',
    method: 'get',
    headers: {'token': token}
  })
  .then(function (response) {
    console.log(response);
    userStore.dispatch({ type: 'LOGIN', user: response.data })
  })
  .catch(function (error) {
    console.log(error);
  });
}

export { auth }



