/* ADD category*/

import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, categoriesStore } from './../../state';

let add = function(categoryName){

  const user = userStore.getState()
  console.log(user)

  console.log(categoriesStore.getState())
  let dummyCateg = {
    name: categoryName,
    _id: 'DUMMY ' + Math.random().toString(36).substring(7)
  }
  let place = categoriesStore.getState().length

  categoriesStore.dispatch({type: 'ADD', category: dummyCateg})

  axios.request({
    url: endpointURL + '/add/category',
    method: 'post',
    headers: {'token': user.token},
    data:{name: categoryName}
  })
  .then(function (response) {
    console.log(response.data);
    categoriesStore.dispatch({type: 'DELETE', index: place})
    categoriesStore.dispatch({type: 'ADD', index: place, category: response.data})
  })
  .catch(function (error) {
    //if something whent wrong, put post back to the store
    //postsStore.dispatch({type: 'ADD', index: index, post: post})
    //categoriesStore.dispatch({type: 'DELETE', index: place})
    console.log(error);
  });
}

export { add }



