import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, postsStore } from './../../state';
import find from './find';

let Delete = function(postId){
  //removing from store
  let [index, post] = find(postId);
  if(!post) return
  console.log(index)
  console.log(post)

  const user = userStore.getState()
  console.log(user)

  postsStore.dispatch({type: 'DELETE', _id: postId})

  axios.request({
    url: endpointURL + '/delete/post',
    method: 'post',
    headers: {'token': user.token},
    data:{_id: postId}
  })
  .then(function (response) {
    console.log(response);
  })
  .catch(function (error) {
    //if something whent wrong, put post back to the store
    postsStore.dispatch({type: 'ADD', index: index, post: post})
    console.log(error);
  });
}

export { Delete }



