import React, { Component } from 'react';
import login from './../connectors/user/login';

class Login extends Component {
  constructor(props) {
   super(props);
   this.state = {
     showError: false,
     error: ''
   };
  }
  login(){

    const username = this.refs.username.value
    const password = this.refs.password.value
    login(username, password, function(res){
      if (res.status !== 200){
        this.setState({
          showError: true,
          error: res.message
        })
      }
    }.bind(this))

  }
  catchEnter(e){
    console.log('hello')
    if(e.keyCode !== 13) return
    this.login()
  }
  render() {
    return (
      <div className="content-center" style={{justifyContent: 'center'}} onKeyUp={this.catchEnter.bind(this)}>
      <div id="login" className="column auth" style={{width: "250px", marginTop: '50px'}}>
        <div className="single errors" style={{visibility: this.state.showError? 'visible' : 'hidden'}}>{this.state.error}</div>
        <input ref="username" className="single" type="text" placeholder="Username"/>
        <input ref="password" className="single" type="password" placeholder="Password"/>
        <button className="single btn" onClick={() => this.login()}>Login!</button>
        <p>Don't have an account? <a href="?register">Regiser!</a></p>
      </div>
    </div>
    );
  }
  }

export default Login;



