import React, { Component } from 'react';
import Tags from './Tags';
import { deletePost, updatePost } from './../connectors/Api';

class Post extends Component{
  archive(){
    console.log('archive')
    let newPost = this.props.post
    newPost.archived = true
    updatePost(newPost)
  }
  delete(){
    console.log('delete')
    deletePost(this.props.post._id)
  }
  star(){
    console.log('star')
    let newPost = this.props.post
    newPost.starred = true
    updatePost(newPost)
  }
  drag(ev) {
    ev.dataTransfer.setData("data", JSON.stringify(this.props.post));
  }
  render(){
    return (
      <div className="post" draggable='true' onDragStart={this.drag.bind(this)}>

      <a className="link" target="_blank" href={this.props.post.url}>
        {this.props.post.title || this.props.post.text || 'Could not get the title of this post :('}
      </a>
      <div className="helpers">
        <span className="archive" onClick={()=> this.archive()}> Archive</span> -
        <span className="star" onClick={()=> this.star()}> Star</span> -
        <span className="delete" onClick={()=> this.delete()}> Delete</span> -
        <span className="tags"><Tags post={this.props.post}/></span>
      </div>
      </div>)
  }
}

export default Post;



