import { createStore } from 'redux';
import { AuthUser } from './../connectors/Api'

function userReducer(state = {}, action){
  switch(action.type){
    case 'LOGIN':
      localStorage.setItem('token', action.user.token)
      localStorage.setItem('verified', action.user.verified)
      return action.user
    case 'LOGGOUT':
      localStorage.removeItem('token')
      localStorage.removeItem('verified')
      return {}
    default:
      return state
  }
}

let userStore = createStore(userReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())
export default userStore

const token = localStorage.getItem('token')
if(token){
  AuthUser(token)
}



