import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore }from './../../state'

const messages = {
  400: 'Please complete the below fields.',
  401: 'Invalid username or password.'
}

let login = function(username, password, callback){
  axios.post(endpointURL + '/auth', {
    username: username,
    password: password
  })
  .then(function (response) {
    console.log(response);
    userStore.dispatch({ type: 'LOGIN', user: response.data })
    const res = {
      status: 200
    }
    callback(res)
  })
  .catch(function (error) {
     const res = {
      status: error.response.status,
      message: messages[error.response.status]
    }
    callback(res)
  });
}

export default login



