import React, { Component } from 'react';

class Main extends Component {
  render() {
    return (
      <div id="main" className="content-center column">

      <h1>Save!</h1>
      <div style={{textAlign: 'center'}}>
        <p>Welcome to Save!,<br></br><br></br> a Chrome companion that helps you <br></br> organize and remember web-content you like.</p>
        <br/>The app is yet under developpement,<br></br> if you have an account, please <a href="?login">login.</a>
      </div>
      </div>
    );
  }
}

export default Main;



