/* Gets all of user's posts from the server */

import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, postsStore } from './../../state';

let get = function(){

  const user = userStore.getState()

  axios.request({
    url: endpointURL + '/get',
    method: 'get',
    headers: {'token': user.token}
  })
  .then(function (response) {
    postsStore.dispatch({type: 'POPULATE', content: response.data})
    //console.log(response.data)
  })
  .catch(function (error) {
    console.log('Could not authenticate, logging out')
    userStore.dispatch({type: 'LOGGOUT'})
  });
}

export { get }



