import React, { Component } from 'react';
import TagsInput from 'react-tagsinput';
import { updatePost } from './../connectors/Api';


class Tags extends Component{
  constructor() {
    super()
    this.state = {
      show: false,
      tags: [],
      tagsAtLastSave: []
    }
  }

  componentWillMount(){
    if(this.props.post.tags) this.setState({tags: this.props.post.tags})
  }

  handleChange(tags) {
    this.setState({tags})
  }

  submit(e){
    if(e.keyCode === 27){
      this.setState({show: false, tags: this.state.tagsAtLastSave})
      return
    }
    if(e.keyCode !== 13) return
    let post = this.props.post
    post.tags = this.state.tags
    updatePost(post)
    this.setState({show: false})
  }
  edit(){
    this.setState({show: true, tagsAtLastSave: this.state.tags})

  }
  renderInput (props) {
  let {onChange, value, ...other} = props
  if (props.disabled) return ''
  return (
    <input style={{width: '200px'}} autoFocus type='text' onChange={onChange} value={value} onKeyUp={this.submit.bind(this)} {...other} placeholder='Add a tag (press return to save)' />
  )
}

  render() {
    var btn = (
      <span style={{display: 'flex', flexDirection: 'row'}}>
      <TagsInput disabled={true} className={'react-tagsinput-disabled'} value={this.state.tags} onChange={this.handleChange.bind(this)} addKeys={[9,13,188]} onlyUnique={true} renderInput={this.renderInput.bind(this)} />
      <a onClick={()=> this.edit()}>{this.state.tags.length > 0? 'Edit tags' : 'Add tags'}</a>
      </span>
    )
    var tags = <TagsInput className={'react-tagsinput'} value={this.state.tags} onChange={this.handleChange.bind(this)} addKeys={[9,13,188]} onlyUnique={true} renderInput={this.renderInput.bind(this)} />

    return this.state.show? tags: btn
  }
}

export default Tags



