import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore } from './../../state'

let Default = function(categId){

  const user = userStore.getState()
  axios.request({
    url: endpointURL + '/default/category',
    method: 'post',
    headers: {'token': user.token},
    data: {_id: categId }
  })
  .then(function (response) {
    //success!!
    console.log('Successfully set category as new default')
  })
  .catch(function (error) {
    // fail
    // need to restore
    console.log(error)
  });
}

export { Default }



