//returns one categ or null by id from the store
import { categoriesStore } from './../../state';

let find = function(id){
  const categs = categoriesStore.getState()
  for(let i = 0, l = categs.length; i < l; i++){
    if(categs[i]._id === id){
      return [i, categs[i]]
    }
  }
  return null
}

let findIndex = function(index){
  const categs = categoriesStore.getState()
  if(categs.length <= index) return null
  return categs[index]
}

export { find, findIndex }



