import { createStore } from 'redux';

function categoriesReducer(state = 'empty', action){
  switch(action.type){
    case 'POPULATE':
      return action.content
    case 'EMPTY':
      return 'empty'
    case 'ADD':
      let index = typeof action.index === 'undefined' || action.index.isNan?  state.length : action.index
      let replace = action.replace || 0
      let x = [...state]
      x.splice(index, replace, action.category)
      return x
    case 'DELETE':
      let y = [...state]
      y.splice(action.index, 1)
      return y
    default:
      return state
  }
}

let categoriesStore = createStore(categoriesReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())

export default categoriesStore



