import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, postsStore } from './../../state';
import find from './find';

let update = function(newPost){
  //removing from store
  let [index, post] = find(newPost._id);
  if(!post) return

  const user = userStore.getState()

  postsStore.dispatch({type: 'UPDATE', post: newPost, index: index})

  axios.request({
    url: endpointURL + '/update/post',
    method: 'post',
    headers: {'token': user.token},
    data: newPost
  })
  .then(function (response) {
    //success!!
    console.log(response )
  })
  .catch(function (error) {
    // fail
    // need to restore
    postsStore.dispatch({type: 'UPDATE', post: post, index: index})
  });
}

export { update }



