import React, { Component } from 'react';

import {postsStore, selectedCategory} from './../state';
import { getUsersPosts } from './../connectors/Api';

import SideMenu from './SideMenu';
import Content from './Content';

class App extends Component {
  constructor(props) {
   super(props);
   this.state = {posts: [], category:{_id: ''}};
  }
  componentWillMount(){
    console.log('Getting posts form the server')
    getUsersPosts()
  }
  getPosts(){
    postsStore.subscribe(()=>{
      this.setState({posts: postsStore.getState()})
    })
    selectedCategory.subscribe(()=>{
      this.setState({category: selectedCategory.getState()})
    })
  }
  render() {
    this.getPosts()
    return (
      <div className="main">
        <SideMenu/>
        <Content posts={this.state.posts.filter(x => x.categoryId === this.state.category._id)}/>
      </div>
    );
  }
}

export default App;



