//returns one post or null by id from the store
import { postsStore } from './../../state';

let find = function(id){
  const posts = postsStore.getState()
  for(let i = 0, l = posts.length; i < l; i++){
    if(posts[i]._id === id){
      return [i, posts[i]]
    }
  }
  return null
}

export default find



