import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore }from './../../state'

let register = function(username, password, email){
  axios.post(endpointURL + '/register', {
    username: username,
    password: password,
    email: email
  })
  .then(function (response) {
    console.log(response);
    userStore.dispatch({ type: 'LOGIN', user: response.data })
  })
  .catch(function (error) {
    console.log(error);
  });
}

export { register }



