import { createStore } from 'redux';

function selectedCategoryReducer(state = 'empty', action){
  switch(action.type){
    case 'SELECT':
      return action.selected
    default:
      return state
  }
}

let selectedCategory = createStore(selectedCategoryReducer)

function categoryViewReducer(state = 'main', action){
  switch(action.type){
    case 'CHANGE':
      return action.to
    default:
      return state
  }
}

let categoryView = createStore(categoryViewReducer)

export { selectedCategory, categoryView }



