import React, { Component } from 'react';
import { RegisterUser} from './../connectors/Api';


class Register extends Component {
  register(){
    const username = this.refs.username.value
    const password = this.refs.password.value
    const email = this.refs.email.value
    RegisterUser(username, password, email)
  }
  render() {
    return (
      <div className="content-center" style={{justifyContent: 'center'}}>
      <div id="login" className="column auth" style={{width: '250px', marginTop: '50px'}}>
        <input ref="username" className="single" type="text" placeholder="Username"/>
        <input ref="email" className="single" type="text" placeholder="Email"/>
        <input ref="password" className="single" type="password" placeholder="Password"/>
        <button className="single btn" onClick={()=>this.register()}>Register!</button>
        <p>Already have an account? <a href="?login">Login!</a></p>
      </div>

    </div>
    );
  }
}

export default Register;



