import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, categoriesStore, selectedCategory } from './../../state';
import { find } from './find';

let rename = function(categId, newName){

  let [index, categ] = find(categId);
  console.log(index)
  //making copy
  let newCateg = JSON.parse(JSON.stringify(categ))
  newCateg.name = newName

  //replaces the old
  categoriesStore.dispatch({type: 'ADD', index: index, replace: 1, category: newCateg})
  selectedCategory.dispatch({type: 'SELECT', selected: newCateg})

  const user = userStore.getState()
  axios.request({
    url: endpointURL + '/rename/category',
    method: 'post',
    headers: {'token': user.token},
    data: {_id: categId, name: newName }
  })
  .then(function (response) {
    //success!!
    console.log('Successfully renamed category!!')
  })
  .catch(function (error) {
    // fail
    // need to restore
    console.log(categ)
    categoriesStore.dispatch({type: 'ADD', index: index, replace: 1, category: categ})
    selectedCategory.dispatch({type: 'SELECT', selected: categ})
    console.log(error)
  });
}

export { rename }



