/* Gets categories from the server */

import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, categoriesStore, selectedCategory } from './../../state';

let get = function(){

  const user = userStore.getState()

  axios.request({
    url: endpointURL + '/categories',
    method: 'get',
    headers: {'token': user.token}
  })
  .then(function (response) {
    categoriesStore.dispatch({type: 'POPULATE', content: response.data})
    selectedCategory.dispatch({type: 'SELECT', selected: response.data[0]})
    console.log(response);
  })
  .catch(function (error) {
    //if something whent wrong, put post back to the store
    //postsStore.dispatch({type: 'ADD', index: index, post: post})
    console.log(error);
  });
}

export { get }



