import React, { Component } from 'react';
import {List, ListItem} from 'material-ui/List';
import Subheader from 'material-ui/Subheader';
import TextField from 'material-ui/TextField';
import FlatButton from 'material-ui/FlatButton';
import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import IconButton from 'material-ui/IconButton';
import MoreVertIcon from 'material-ui/svg-icons/action/settings';

import { userStore, categoriesStore, selectedCategory, categoryView } from './../state';
import { addCategory, getCategories, deleteCategory, defaultCategory, renameCategory, updatePost } from './../connectors/Api';


class Logout extends Component{
  logout(){
    userStore.dispatch({ type: 'LOGGOUT' })
  }
  render(){
    return (
      <button className="btn" onClick={()=> this.logout()}>Logout</button>
    )
  }
}

class AddCategory extends Component {
  add(){
    let input = document.getElementById('newCategory')
    addCategory(input.value)


    input.value = null
    input.blur()
  }
  watchKeys(e){
    if(e.keyCode !== 13) return
    this.add()
  }
  render(){
  return (
    <TextField
      id="newCategory"
      hintText="Add category"
      onKeyDown={this.watchKeys.bind(this)}
      fullWidth={true}
      inputStyle={{paddingLeft: '10px'}}
      hintStyle={{paddingLeft: '10px'}}
    />
  );
}
}

class SelectedCategory extends Component {

  constructor(props){
    super(props);
    this.state= {
      rename: false,
      selectedIndex: 0,
    }
  }
  select(index){
    this.setState({selectedIndex: index})
  }

  toggleRename(){
    this.setState({rename: !this.state.rename})
  }
  rename(e){
    // we got escape
    if(e.keyCode === 27){
      this.setState({rename: false})
      return
    }
    // only continue if we have return keyCode
    if(e.keyCode !== 13) return
    console.log(this.refs.newName.value)
    renameCategory(this.props._id, this.refs.newName.value)
    this.setState({rename: false})

  }
  showMain(){ categoryView.dispatch({type: 'CHANGE', to: 'main'}) }
  showStarred(){ categoryView.dispatch({type: 'CHANGE', to: 'starred'}) }
  showArchived(){ categoryView.dispatch({type: 'CHANGE', to: 'archived'}) }
  deleteCateg(id){ deleteCategory(id) }
  defaultCateg(id){ defaultCategory(id) }
  render(){
    return(
      <div id="choosen" style={{textAlign: 'center'}}>
        {this.state.rename? <input ref='newName' className="title" type="text" defaultValue={this.props.name} onKeyUp={this.rename.bind(this)} onBlur={this.toggleRename.bind(this)} autoFocus/>
        : <p className="title" onClick={()=>this.showMain()} onDoubleClick={()=>this.toggleRename()}>{this.props.name}</p>}

        <div className="inline">
          <IconMenu
            iconButtonElement={
              <IconButton style={{height: '0px', paddingTop: '5px'}}><MoreVertIcon /></IconButton>
            }
            anchorOrigin={{horizontal: 'left', vertical: 'top'}}
            targetOrigin={{horizontal: 'left', vertical: 'top'}}
            onKeyboardFocus={()=>{return}}
            useLayerForClickAway={true}
          >
            <MenuItem primaryText="Set as default" onClick={()=>this.defaultCateg(this.props._id)}/>
            <MenuItem primaryText="Delete category" onClick={()=>this.deleteCateg(this.props._id)}/>
          </IconMenu>
          <FlatButton label="Starred" style={{minWidth: '0px'}} onClick={()=>this.showStarred()}/>
          <FlatButton label="Archived" style={{minWidth: '0px'}} onClick={()=>this.showArchived()}/>
        </div>

      </div>
    )
  }
}

class SideMenu extends Component {
  constructor(props) {
   super(props);
   this.state = {
     rename: false,
     categories: [],
     selected: {name: 'none'}
   }
  }
  getCategs(){
    categoriesStore.subscribe(()=>{
      this.setState({categories: categoriesStore.getState()})
    })
    selectedCategory.subscribe(()=>{
      this.setState({selected: selectedCategory.getState()})
    })
  }
  componentWillMount(){
    this.getCategs();
    getCategories();
  }
  allowDrop(ev) {
    ev.preventDefault();
  }
  drop(ev, categId) {
      ev.preventDefault();
      let data = JSON.parse(ev.dataTransfer.getData("data"));
      data.categoryId = categId
      updatePost(data)
      console.log(data)
  }
  selectCategory(categ){
    selectedCategory.dispatch({type: 'SELECT', selected: categ})
  }
  render(){
    return (
      <div id="side-menu">
        <SelectedCategory {...this.state.selected}/>
        <div id="categories">
        <List>
        <Subheader>Categories</Subheader>
        {
          this.state.categories.map((category)=>
            <ListItem primaryText={category.name} key={category._id} onDrop={(event) => {this.drop(event, category._id)}} onDragOver={this.allowDrop} onClick={()=>{this.selectCategory(category)}} />
          )
        }
        <AddCategory/>
        </List>

        <Logout/>
        </div>
      </div>
    )
  }
}

export default SideMenu



