import { createStore } from 'redux';

function postsReducer(state = 'empty', action){
  switch(action.type){
    case 'POPULATE':
      return action.content
    case 'EMPTY':
      return 'empty'
    case 'UPDATE':
      let x = [...state]
      x.splice(action.index, 1, action.post)
      console.log(state)
      return x
    case 'DELETE':
      return state.filter(function(item) {
        return item._id !== action._id;
      });
    case 'ADD':
      x = [...state]
      x.splice(action.index, 0, action.post)
      console.log(state)
      return x
    default:
      return state
  }
}

let postsStore = createStore(postsReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())

export default postsStore



