import React, { Component } from 'react';

import { deletePost } from './../connectors/Api';
import { categoryView } from './../state';
import Post from './Post';

class Note extends Component{
  archive(){
    console.log('archive')
  }
  delete(){
    console.log('delete')
    deletePost(this.props.post._id)
  }
  star(){
    console.log('star')
  }
  render(){
    console.log('hey')
    return (
      <div className="note">

      <p className="link">
        {this.props.post.text}
      </p>
      <div>From: <a target="_blank" href={this.props.post.url}>
        {this.props.post.title}
      </a></div>
      <div className="helpers">
        <span className="archive" onClick={()=> this.archive()}> Archive</span> -
        <span className="star" onClick={()=> this.star()}> Star</span> -
        <span className="delete" onClick={()=> this.delete()}> Delete</span> -
        Add taggs
      </div>
      </div>)
  }
}

class Content extends Component{
  constructor(props) {
   super(props);
   this.state = {
     view: 'main'
   };
  }
  componentWillMount(){
    categoryView.dispatch({type: 'CHANGE', to: 'main'})
  }
  componentDidMount(){
    categoryView.subscribe(()=>{
      this.setState({view: categoryView.getState()})
    })
  }
  viewTranslator(){
    //main, starred, archived
    let view = this.state.view
    switch(view){
      case 'main':
        return this.props.posts.filter(x => x.archived !== true)
      case 'starred':
        return this.props.posts.filter(x => x.starred === true)
      case 'archived':
        return this.props.posts.filter(x => x.archived === true)
      default:
        return this.props.posts.filter(x => x.archived !== true)
    }
  }
  generatePosts(){
      return this.viewTranslator().map((p)=> {
        if(p.isText){
          return <Note key={p._id} post={p}/>
        }else if(p.isImg){
          return <p key={p._id}>Image!</p>
        }
        return <Post key={p._id} post={p}/>
      })
  }
  render(){
    return (<div id="content">
      {this.generatePosts()}
    </div>)
  }
}

export default Content



