/* DELETE category*/

import axios from 'axios';
import { endpointURL } from './../../settings';
import { userStore, categoriesStore, selectedCategory } from './../../state';
import { find, findIndex } from './find';

let Delete = function(categId){
  //removing from store
  let [index, categ] = find(categId);
  if(!categ) return
  let nextCateg = findIndex(index + 1)
  if (nextCateg === null) nextCateg = findIndex(index-1)
  console.log(index)
  console.log(categ)

  const user = userStore.getState()
  console.log(user)

  selectedCategory.dispatch({type: 'SELECT', selected: nextCateg})
  categoriesStore.dispatch({type: 'DELETE', index: index})


  axios.request({
    url: endpointURL + '/delete/category',
    method: 'post',
    headers: {'token': user.token},
    data:{_id: categId}
  })
  .then(function (response) {
    console.log(response);
  })
  .catch(function (error) {
    //if something whent wrong, put post back to the store
    categoriesStore.dispatch({type: 'ADD', index: index, categ: categ})
    console.log(error);
  });
}

export { Delete }



